
html,body{margin:0;min-height:100vh
}
body{font-size:12px;line-height:1.5em
}
.v-spinner{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;position:absolute;top:0;bottom:0;left:0;right:0;background:#232D4B;z-index:99
}
.tablet{display:none
}
ul{list-style-type:none;padding:0;text-align:left
}
p{margin:0
}
a{color:inherit;text-decoration:none
}
a:hover,a:active,a:focus{text-decoration:none;color:inherit
}
.notification-balloon{display:block;width:20px;height:20px;border-radius:50%;background:#5FC3A5;color:#232D4B;position:absolute;text-align:center;line-height:22px
}
@media screen and (max-width: 768px){
.notification-balloon{width:16px;height:16px;line-height:16px
}
}
.dropdown-menu{color:#8794C4;background:#191E37;width:100%;margin:0;border-radius:0;background-clip:padding-box;border-top:1px solid #2E3D60;box-shadow:0 0 8px -2px #000;padding:0
}
.dropdown-menu li a{display:block;color:inherit;text-transform:uppercase;font-size:11px;letter-spacing:0.1em;padding:14px 12px 11px
}
.dropdown-menu li a:hover{color:#D8DAE5;background-color:#646EA0
}
.new-box{height:198px;width:25%;border:1px dashed #5FC3A5;text-align:center;line-height:198px;text-transform:uppercase;display:block;color:#5FC3A5;margin:0 12.5px 30px
}
.new-box:hover{border:1px dashed #fff
}
.second-col-bg{background:#5FC3A5 !important
}
.warning-col-bg{background:#CCBA54 !important
}
.danger-col-bg{background:#C87369 !important
}
#app{font-family:'Poppins', sans-serif;color:#D8DAE5;height:100%;padding:0 0 0 210px;min-height:100vh
}
@media screen and (max-width: 768px){
#app{padding:0 0 0 75px
}
}
#app .page{padding:0 20px;background:#232D4B;min-height:100vh
}



