
.attachments{margin:0 0 20px
}
.attachment{padding:10px 20px;background-color:#2E3D60;margin-top:3px;color:#5FC3A5;position:relative
}
.attachment .view-icon{display:inline;margin-right:10px;position:relative;top:3px
}
.attachment .view-icon svg{width:18px
}
.attachment i.fa{position:absolute;right:20px;top:16px;color:#C87369;cursor:pointer;transition:opacity .2s ease-in
}
.attachment i.fa:hover,.attachment i.fa:active{opacity:.8;filter:alpha(opacity=80)
}
.attachment .attachment-name a{cursor:pointer
}



