
.gantt_add{display:none !important
}
.gantt-chart{width:100%;height:100%;min-height:600px
}
.gantt_container{font-family:Poppins;font-size:10px;border:1px solid #646EA0;text-transform:uppercase;letter-spacing:0.1em
}
.gantt_grid{border-right:1px solid #646EA0
}
.gantt_grid_scale,.gantt_task_scale{color:#8794C4;font-size:10px;border-bottom:1px solid #191E37;background-color:#191E37
}
.gantt_grid_scale .gantt_grid_head_cell{color:#8794C4
}
.gantt_row,.gantt_task_row,.gantt_row.odd,.gantt_task_row.odd{border-bottom:1px solid #232D4B;background-color:#232D4B
}
.gantt_task .gantt_task_scale .gantt_scale_cell{color:#8794C4;border-right:1px solid #191E37
}
.gantt_task_line{border-radius:3px;background-clip:padding-box;background-color:#5FC3A5;border:1px solid #232D4B
}
.gantt_task_progress{background:#197252;border-top-left-radius:3px;border-top-right-radius:0;border-bottom-right-radius:0;border-bottom-left-radius:3px;-webkit-border-top-left-radius:3px;-webkit-border-top-right-radius:0;-webkit-border-bottom-right-radius:0;-webkit-border-bottom-left-radius:3px;-moz-border-radius-topleft:3px;-moz-border-radius-topright:0;-moz-border-radius-bottomright:0;-moz-border-radius-bottomleft:3px
}
.gantt_task_content{font-size:10px;font-weight:500
}
.gantt_task_cell{border-right:1px solid #232D4B
}
.gantt_grid_data .gantt_cell{color:#D8DAE5;cursor:pointer
}
.gantt_cell{font-size:10px;font-weight:500
}
.gantt_grid_data .gantt_row.odd:hover,.gantt_grid_data .gantt_row:hover{background-color:#191E37
}
.gantt_grid_data .gantt_row.gantt_selected,.gantt_grid_data .gantt_row.odd.gantt_selected,.gantt_task_row.gantt_selected{background-color:#191E37
}
.gantt_task_row.gantt_selected .gantt_task_cell{border-right-color:#191E37
}
.gantt_line_wrapper div{background-color:#8794C4
}
.gantt_link_arrow_right{border-color:#8794C4
}
.gantt_task_line.gantt_selected{box-shadow:0 0 7px #111526
}
.gantt_tree_icon.gantt_close:before{content:"-"
}
.gantt_tree_icon.gantt_open:before{content:"+"
}
.gantt_tree_icon.gantt_close,.gantt_tree_icon.gantt_open{background:none;width:15px
}
.gantt_tree_icon.gantt_close:before,.gantt_tree_icon.gantt_open:before{width:15px;height:15px;border:1px solid #8794C4;color:#8794C4;display:inline-block;line-height:14px;text-align:center
}



