
.box{font-weight:300;background:#191E37;padding:20px;color:#D8DAE5;letter-spacing:1px;margin:0 0 30px;position:relative
}
.box.border-color-1{border-bottom:4px solid #E2C94D
}
.box.border-color-2{border-bottom:4px solid #E04FCC
}
.box.border-color-3{border-bottom:4px solid #50C9DD
}
.box.border-color-4{border-bottom:4px solid #5EDD50
}
.box.border-color-5{border-bottom:4px solid #D87F52
}
.box .box-header{border-bottom:1px solid #646EA0;padding:0 60px 13px 0;position:relative
}
.column .box{margin-bottom:10px
}
.icons{display:-ms-flexbox;display:flex
}
.icons .icon-holder{display:-ms-flexbox;display:flex;margin-right:16px;-ms-flex-item-align:end;align-self:flex-end
}
.icons .icon-holder .number{-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center
}
table th{color:#646EA0;text-transform:uppercase;font-weight:400;width:100%
}
table .odd{background:#232D4B
}
table .even{background:#2E3D60
}
.new{position:relative
}
.new .tag{position:absolute;top:-2px;left:-2px;border:20px solid transparent;border-left-color:#5FC3A5;border-top-color:#5FC3A5
}
.new .tag span{position:absolute;font-size:9px;top:-11px;transform:rotate(-45deg);left:-17px;text-transform:uppercase;color:#197252;font-weight:500
}
.widget .box{background-color:#232D4B
}
.task-box .box-header{border-bottom:1px solid #646EA0;padding-bottom:15px
}
.task-box .box-header h2{color:#5FC3A5;font-size:12px;font-weight:700;text-transform:uppercase;letter-spacing:1.6px;margin:8px 0 5px
}
.task-box .box-header .user-info .user-avatar{width:30px;height:30px;border-radius:50%;background-clip:padding-box;margin-right:5px
}
.task-box .box-header .status-boxes,.task-box .box-header .favourite{position:absolute;top:0;right:0
}
.task-box .box-header .task-id{color:#646EA0;font-size:11px;margin:0
}
.info{padding:18px 0 5px
}
.info .plan{margin-bottom:15px
}
.info .plan a{font-size:12px;color:#D8DAE5;transition:color .2s ease
}
.info .plan a:hover,.info .plan a:active{color:#5FC3A5
}
.status p{text-transform:uppercase;margin-bottom:8px;font-size:8px;line-height:12px
}
.status p span{color:#646EA0
}
.status-box{display:inline-block;width:18px;height:18px;margin-left:2px;border-radius:50%;background-clip:padding-box;background:#232D4B;cursor:pointer;opacity:1;filter:alpha(opacity=100);transition:opacity .2s ease-in
}
.status-box:first-child{margin-left:0
}
.status-box:hover{opacity:.8;filter:alpha(opacity=80)
}
.nicescroll-cursors{border:none !important;border-radius:0 !important;width:2px !important;background:#646EA0 !important
}
h2{line-height:15px
}
table{margin:0 -10px;white-space:nowrap
}
table td{font-size:10px
}
table th,table td{padding:3px 9px
}
.st0{fill:#646EA0
}
.progress-line.bar-chart{margin-top:40px
}
.nicescroll{margin:20px 0;padding-right:10px;max-height:100px;overflow:hidden
}
.bullets li{margin-bottom:19px
}
.info.bottom{padding-top:0
}



