
.calendar{width:100%;background:#191E37;border:1px solid #191E37;border-top:1px solid #232D4B;box-shadow:0 0 30px #111526
}
.calendar header .next:not(.disabled):hover,.calendar header .prev:not(.disabled):hover,.calendar header .up:not(.disabled):hover{background:#5FC3A5;color:#fff
}
.calendar header .prev:after{border-right:10px solid #8794C4
}
.calendar header .prev:not(.disabled):hover:after{border-right:10px solid #fff
}
.calendar header .next:after{border-left:10px solid #8794C4
}
.calendar header .next:not(.disabled):hover:after{border-left:10px solid #fff
}
.calendar .cell.selected{background:#5FC3A5;color:#fff
}
.calendar .cell.selected.highlighted{background:#5FC3A5
}
.calendar .cell.selected:hover{background:#5FC3A5
}
.calendar .cell:not(.blank):not(.disabled).day:hover,.calendar .cell:not(.blank):not(.disabled).month:hover,.calendar .cell:not(.blank):not(.disabled).year:hover{border:1px solid #197252
}



